footer {
  background-color: #131a43;
}

.lines {
  width: 100%;
  position: relative;
}

.lines::after,
.lines::before {
  content: "";
  position: absolute;
  margin: auto;
  height: 2px;
  background: rgb(125, 223, 125);
  width: 49%;
  top: 45%;
}

.lines::after {
  left: 0;
  /* right: 10px; */
}

.lines::before {
  right: 0;
  /* left:10px; */
}

.diamond {
  height: 15px;
  width: 15px;
  border: 2px solid rgb(125, 223, 125);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  transform: rotate(45deg);
}
