
.sponsors-hero-container
{
 flex:1;
 margin:0px;
 position:relative;
}
.sponsors-header-img
{
width:100%;
height:100vh;
object-fit: cover;
/* ${mobile({ height : "30vh"})} */
}

.sponsors-hero-info
{
position: absolute;
width: 100%;
height: 100%;
top : 0;
left : 0;
display: flex;
flex-direction: column;
/* justify-content: center; */
/* align-items: center; */

}
.sponsors-hero-title 
{
color: white;
}

.scroll-to-main{
  animation: scrollMain 0.5s alternate ease infinite;
}

@keyframes scrollMain {
  from{
    transform: translateY(2px);
  }
  to{
    transform: translateY(-1px);
  }
}

.slick-slide >div{
  margin: 0 10px;
}

.slick-list{
  margin: 0 -10px;
}
