.lines{
  width:100%;
  position: relative;
}

.lines::after, .lines::before{
  content:'';
  position: absolute;
  margin:auto;
  height:2px;
  background:white;
  width:49%;
  top:45%;
}

.lines::after{
  left:0;
  /* right: 10px; */
}

.lines::before{
  right:0;
  /* left:10px; */
}

.diamond{
  height: 15px;
  width:15px;
  border:2px solid white;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin:auto;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.scroll-to-main{
  animation: scrollMain 0.5s alternate ease infinite;
}

@keyframes scrollMain {
  from{
    transform: translateY(2px);
  }
  to{
    transform: translateY(-1px);
  }
}

.slick-slide >div{
  margin: 0 10px;
}

.slick-list{
  margin: 0 -10px;
}
