
.item2{
  color: #252b37;
  background-color: #151823;
  animation: bgColor 5s ease infinite;
}

@keyframes animateGlow {
  0% {
    background-position: 0% 50%;
 }
  50% {
    background-position: 100% 50%;
 }
  100% {
    background-position: 0% 50%;
 }
}
@keyframes bgColor {
  0% {
    background-color: #aa61fd;
 }
  25% {
    background-color: #0fffc1;
 }
  50% {
    background-color:#5982d3;
 }
 75% {
  background-color:#61bffd;
}
100% {
  background-color: #aa61fd;
}
}

.blinking {
  -webkit-animation: 1s blink ease infinite;
  -moz-animation: 1s blink ease infinite;
  -ms-animation: 1s blink ease infinite;
  -o-animation: 1s blink ease infinite;
  animation: 1s blink ease infinite;
  
}

@keyframes blink {
  from, to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-moz-keyframes blink {
  from, to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-webkit-keyframes blink {
  from, to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-ms-keyframes blink{
  from, to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-o-keyframes blink {
  from, to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

.banner-card {
  @apply relative flex items-center justify-center  overflow-hidden shadow-xl w-full h-60 rounded-xl;
}

.banner-card img {
  @apply absolute w-full h-full transition-all duration-500 ease-in-out transform bg-center bg-cover;
}

.banner-card:hover img {
  @apply scale-150;
}
