.center{
    margin: auto;
    width: 60%;
    padding: 10px;
}

.for_image{
    width: 70%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.for_content{
    font-size: larger;
    color: rgb(227, 145, 227);
}

x-sign {
    --interval: 1s;
    display: block;
    text-shadow: 
      0 0 10px var(--color1),
      0 0 20px var(--color2),
      0 0 40px var(--color3),
      0 0 80px var(--color4);
    will-change: filter, color;
    filter: saturate(60%);
    animation: flicker steps(100) var(--interval) 1s infinite;
    font-size: 3rem;
    text-align: center;
}

x-sign:nth-of-type(1) {
    color: tomato;
    --color1: orangered;
    --color2: firebrick;
    --color3: maroon;
    --color4: darkred;
    font-family: Yellowtail;
  }

  
@keyframes flicker {
    50% {
      color: white;
      filter: saturate(200%) hue-rotate(20deg);
    }
  }